.p {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid #5e6cad;
    border-radius: 10px;
    overflow: hidden;
}

.p-browser {
    height: 20px;
    background-color: #5e6cad;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: aliceblue;
}

.p-img {
    width: 100%;
    transition: all 10s ease;
}

.p:hover .p-image {
    transform: translateY(-100%)
}


@media screen and (max-width: 480px) {
    .p {
        width: 40%;
        height: 20vh;
    }
}